import React from 'react';
import { Icon } from 'antd';
import { Map, ServerItemOS } from '../interface';

const icons: Map = {
  windows: 'data:image/png;base64,',
  darwin: 'data:image/png;base64,',
  linux: 'data:image/png;base64,',
  freebsd: 'data:image/png;base64,',
  centos: 'data:image/png;base64,',
  fedora: 'data:image/png;base64,',
  debian: 'data:image/png;base64,',
  ubuntu: 'data:image/png;base64,',
  suse: 'data:image/png;base64,',
  unknown: 'data:image/png;base64,',
};

const XPlatform: React.FC<ServerItemOS> = (props: ServerItemOS) => {
  const { name, version, arch } = props;

  let title = 'unknown';
  if (typeof version !== 'undefined') {
    if (version === 'unknown') {
      title = `${name} (${arch})`;
    } else {
      title = `${name} ${version} (${arch})`;
    }
  }

  const svg = () => (
    <svg
      version="1.1"
      x="0px"
      y="0px"
      viewBox="0 0 1024 1024"
    // enableBackground="new 0 0 1024 1024"
    >
      <image
        width="100%"
        height="100%"
        x="0"
        y="0"
        href={icons[name] === undefined ? icons.unknown : icons[name]}
      />
    </svg>
  );

  return <span title={title}><Icon component={svg} style={{ width: '20px', height: '20px' }} /></span>;
};

export default XPlatform;
